import { dictionary } from "./dictionary";

Office.onReady((info) => {
  console.log(`Office está listo. Host: ${info.host}, Plataforma: ${info.platform}`);
  
  // Configurar el botón según la aplicación
  const btnNormalizar = document.getElementById("btn-normalizar");
  
  if (btnNormalizar) {
    if (info.host === Office.HostType.Word) {
      console.log("Configurando para Word...");
      btnNormalizar.addEventListener("click", normalizarTextoWord);
    } else if (info.host === Office.HostType.PowerPoint) {
      console.log("Configurando para PowerPoint...");
      btnNormalizar.addEventListener("click", normalizarTextoPowerPoint);
    } else if (info.host === Office.HostType.Outlook) {
      console.log("Configurando para Outlook...");
      btnNormalizar.addEventListener("click", normalizarTextoOutlook);
    } else {
      console.error("Aplicación no soportada:", info.host);
      document.body.innerHTML = `
        <div style="padding: 20px; text-align: center;">
          <h2>⚠️ Aplicación no soportada</h2>
          <p>Este complemento solo funciona en Word, PowerPoint y Outlook.</p>
        </div>
      `;
    }
  }
});

// ==========================================
// FUNCIÓN PARA WORD (Compatible con Desktop y Web)
// ==========================================
async function normalizarTextoWord() {
  try {
    await Word.run(async (context) => {
      const body = context.document.body;

      for (const rule of dictionary) {
        // Ordenar los matches de más largo a más corto para procesar primero los más específicos
        const sortedMatches = [...rule.matches].sort((a, b) => b.length - a.length);
        
        // Construir el texto completo de reemplazo
        let fullReplacementText = "";
        for (const part of rule.replacement) {
          fullReplacementText += part.text;
        }
        
        // Procesar cada match de más largo a más corto
        for (const match of sortedMatches) {
          // Buscar todas las ocurrencias de este match
          const results = body.search(match, {
            matchCase: false,
            matchWholeWord: false
          });

          context.load(results, "items");
          await context.sync();

          // Procesar cada resultado encontrado
          // IMPORTANTE: Procesamos de atrás hacia adelante para evitar problemas con las posiciones
          for (let i = results.items.length - 1; i >= 0; i--) {
            const range = results.items[i];
            
            // Cargar el texto actual del rango
            context.load(range, "text");
            await context.sync();
            
            // Solo procesar si el texto NO es ya el resultado esperado
            // Esto evita reemplazos duplicados
            if (range.text !== fullReplacementText) {
              // Eliminar el contenido actual
              range.delete();
              
              // Insertar cada parte secuencialmente con su formato
              let lastRange = null;
              for (let j = 0; j < rule.replacement.length; j++) {
                const part = rule.replacement[j];
                
                if (j === 0) {
                  lastRange = range.insertText(part.text, Word.InsertLocation.start);
                  lastRange.font.bold = part.bold;
                } else {
                  lastRange = lastRange.insertText(part.text, Word.InsertLocation.after);
                  lastRange.font.bold = part.bold;
                }
              }
              
              await context.sync();
            }
          }
        }
      }

      await context.sync();
      console.log("✅ Texto normalizado exitosamente en Word");
    });
  } catch (error) {
    console.error("Error en Word:", error);
    alert("Error al normalizar el texto en Word: " + error.message);
  }
}

// ==========================================
// FUNCIÓN PARA POWERPOINT
// ==========================================
async function normalizarTextoPowerPoint() {
  try {
    console.log("🔄 Iniciando normalización en PowerPoint...");
    
    await PowerPoint.run(async (context) => {
      const slides = context.presentation.slides;
      slides.load("items");
      await context.sync();

      console.log(`📊 Encontradas ${slides.items.length} diapositivas`);
      
      let cambiosRealizados = 0;

      // Procesar cada diapositiva
      for (let i = 0; i < slides.items.length; i++) {
        const slide = slides.items[i];
        const shapes = slide.shapes;
        shapes.load("items");
        await context.sync();

        console.log(`🔍 Diapositiva ${i + 1}: ${shapes.items.length} formas encontradas`);

        // Procesar cada forma (cuadro de texto) en la diapositiva
        for (let j = 0; j < shapes.items.length; j++) {
          try {
            const shape = shapes.items[j];
            
            // Intentar obtener el textFrame directamente
            const textFrame = shape.textFrame;
            const textRange = textFrame.textRange;
            
            // Cargar el texto
            context.load(textRange, "text");
            await context.sync();

            const textoOriginal = textRange.text;
            console.log(`  📝 Forma ${j + 1}: Texto encontrado: "${textoOriginal}"`);

            // Si no hay texto, continuar con la siguiente forma
            if (!textoOriginal || textoOriginal.trim() === "") {
              continue;
            }

            // Aplicar cada regla del diccionario
            for (const rule of dictionary) {
              // Ordenar los matches de más largo a más corto
              const sortedMatches = [...rule.matches].sort((a, b) => b.length - a.length);
              
              // Construir el texto completo esperado
              let expectedText = "";
              for (const part of rule.replacement) {
                expectedText += part.text;
              }
              
              // Buscar y reemplazar cada variante
              for (const match of sortedMatches) {
                // Escapar caracteres especiales de regex
                const escapedMatch = match.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
                const regex = new RegExp(escapedMatch, 'gi');
                
                if (regex.test(textoOriginal)) {
                  console.log(`  ✨ Match encontrado: "${match}" → "${expectedText}"`);
                  
                  // Reemplazar el texto
                  let newText = textoOriginal.replace(regex, expectedText);
                  textRange.text = newText;
                  
                  await context.sync();
                  cambiosRealizados++;
                  
                  console.log(`  ✅ Texto actualizado a: "${newText}"`);
                  break; // Salir después del primer match para evitar reemplazos múltiples
                }
              }
            }
          } catch (shapeError) {
            // Si esta forma no tiene texto, simplemente continuar
            console.log(`  ⚠️ Forma ${j + 1}: No tiene texto o no es accesible`);
          }
        }
      }

      await context.sync();
      
      if (cambiosRealizados > 0) {
        console.log(`✅ Normalización completada. ${cambiosRealizados} cambio(s) realizado(s).`);
        console.log("ℹ️ NOTA: PowerPoint no soporta formato de negrilla parcial.");
      } else {
        console.log("ℹ️ No se encontraron coincidencias para reemplazar.");
        console.log("💡 Verifica que el texto escrito coincida con las palabras del diccionario.");
      }
    });
  } catch (error) {
    console.error("❌ Error en PowerPoint:", error);
    console.error("📋 Detalles del error:", error.message);
  }
}

// ==========================================
// FUNCIÓN PARA OUTLOOK
// ==========================================
async function normalizarTextoOutlook() {
  try {
    const item = Office.context.mailbox.item;
    
    // Verificar si estamos en modo de redacción (compose)
    if (item.itemType === Office.MailboxEnums.ItemType.Message) {
      // En modo de redacción, podemos editar el cuerpo
      item.body.getAsync(Office.CoercionType.Html, async (result) => {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
          let bodyContent = result.value;
          
          // Aplicar cada regla del diccionario
          for (const rule of dictionary) {
            // Ordenar los matches de más largo a más corto
            const sortedMatches = [...rule.matches].sort((a, b) => b.length - a.length);
            
            // Construir el texto completo esperado con HTML para negrilla
            let expectedText = "";
            for (const part of rule.replacement) {
              if (part.bold) {
                expectedText += `<strong>${part.text}</strong>`;
              } else {
                expectedText += part.text;
              }
            }
            
            // Buscar y reemplazar cada variante
            for (const match of sortedMatches) {
              // Escapar caracteres especiales de regex
              const escapedMatch = match.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
              const regex = new RegExp(escapedMatch, 'gi');
              
              if (regex.test(bodyContent)) {
                bodyContent = bodyContent.replace(regex, expectedText);
              }
            }
          }
          
          // Establecer el nuevo contenido del cuerpo
          item.body.setAsync(bodyContent, { coercionType: Office.CoercionType.Html }, (asyncResult) => {
            if (asyncResult.status === Office.AsyncResultStatus.Failed) {
              console.error("Error al actualizar el cuerpo del correo:", asyncResult.error.message);
              alert("Error al normalizar el texto: " + asyncResult.error.message);
            } else {
              console.log("✅ Texto normalizado exitosamente en Outlook");
              alert("Texto normalizado exitosamente en Outlook");
            }
          });
        } else {
          console.error("Error al obtener el cuerpo del correo:", result.error.message);
          alert("Error al obtener el contenido del correo: " + result.error.message);
        }
      });
    } else {
      // En modo de lectura, no podemos editar
      alert("Para normalizar texto en Outlook, debes estar redactando un correo nuevo o respondiendo/reenviando uno existente.");
    }
  } catch (error) {
    console.error("Error en Outlook:", error);
    alert("Error al normalizar el texto en Outlook: " + error.message);
  }
}
